import React, { useEffect, useState } from 'react'
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import axios from 'axios';
import { Button } from '@mui/material';
import styled from '@emotion/styled';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));








const CourseView = () => {
    var[course,setCourse]=useState([]);

    useEffect(()=>{
      // axios.get("http://localhost:3001/api/view")
      // .then((response)=>{
      //     console.log(response.data);
      //     setCourse(course=response.data);
      // })
      fetchCourse()
    },[])

const fetchCourse=()=>{
    axios.get("/api/view")
    .then((response)=>{
        console.log(response.data);
        setCourse(course=response.data);
    })
}

const deleteCourse= ((id)=>{
  //alert(id)
    //console.log(id);
    const data={"_id":id}   //{ params: { id: itemId } }
    axios.post("/api/delete",data)
    .then((response)=>{
        console.log(response.data);
        alert("Successfully Deleted")
        //const json=response.data.json()
        //alert("successfully deleted");
        fetchCourse();
    })

})
const updateCourse=((id)=>{
  console.log(id);
  //const data={"_id":id}
//update code goes here


  // axios.post("http://localhost:3001/api/delete",data)
  //   .then((response)=>{
  //       console.log(response.data);
  //       alert("successfully deleted");
  //       fetchCourse();
  //   })

})


  return (
    <div>
       <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Course Name</TableCell>
            <TableCell align="right">Course Description</TableCell>
            <TableCell align="right">Course Duration</TableCell>
            <TableCell align="right">Course Start Date</TableCell>
            <TableCell align="right">Delete</TableCell>
            <TableCell align="right">Update</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {course.map((value,index) => (
            
            <TableRow
              key={index}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {value.cName}
              </TableCell>
              <TableCell align="right">{value.cDesc}</TableCell>
              <TableCell align="right">{value.cDuration}</TableCell>
              <TableCell align="right">{value.cStartdate}</TableCell>
              <TableCell align="right"><Button onClick={()=>deleteCourse(value._id)} variant='contained' color='error'>Delete </Button></TableCell>
              <TableCell align="right"><Button onClick={()=>updateCourse} variant=''>Update </Button></TableCell>
            </TableRow>
            )
)
          }   
        </TableBody>
      </Table>
    </TableContainer>
    
    </div>
  )
}

export default CourseView
