import React from 'react'
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { Link } from 'react-router-dom';
import { CssBaseline } from '@mui/material';
//import { makeStyles } from '@material-ui/core/styles';

const Header = () => {
  return (
    <div>
       <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static" color='secondary'>
        <Toolbar>
        
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            ICTAK Courses
          </Typography>
          <Button color='inherit'><Link to='/' className='classes.link'>Courses</Link></Button>
          <Button color='inherit'><Link to='/create' className='classes.link'>Add Course</Link></Button>
          <Button color='inherit'><Link to='/search' className='classes.link'>Search Course</Link></Button>
          {/* <Button color="inherit">Login</Button> */}
        </Toolbar>
      </AppBar>
    </Box> 


    </div>
  )
}

export default Header
