import { Button, Table, TableBody, TableCell, TableHead, TableRow, TextField, Typography } from '@mui/material'
import axios from 'axios';
import React, { useState } from 'react'
import useForm from './useForm'

const CourseAdd = () => {
    var[course,setCourse]=useForm({cName:"",cDesc:"",cDuration:"",cStartdate:"00-00-0000"});
    var[result,setResult]=useState({})
    
    const handleSubmit= async (e)=>{
      e.preventDefault()
      console.log(course);
      //form validation
      try{
await axios.post("/api/create",course)

.then((response)=>{
  console.log(response.data);
  //result=response.data;
  // setResult(...result,course)
  setResult(result=response.data)
  
})
alert("success")

      }
      catch(error){
console.log(error);
      }
      setCourse("")
    }

  return (
    <div>
      <Typography variant='h3'>Add Course</Typography>
      <TextField value={course.cName}  onChange={setCourse} name='cName' variant='outlined' label='Course Name' fullWidth margin='normal'></TextField>
      <TextField value={course.cDesc}  onChange={setCourse} name='cDesc' variant='outlined' label='Course Description' fullWidth margin='normal'></TextField>
      <TextField value={course.cDuration}  onChange={setCourse} name='cDuration' variant='outlined' label='Duration' fullWidth margin='normal'></TextField>
      <TextField type= 'date' value={course.cStartdate}  onChange={setCourse} name='cStartdate' variant='outlined' label='Start Date' fullWidth margin='normal'></TextField>
      
    
    
      <Button variant='contained' color='primary' onClick={handleSubmit}>Add Course</Button>

      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Course Name</TableCell>
            <TableCell align="right">Course Description</TableCell>
            <TableCell align="right">Course Duration</TableCell>
            <TableCell align="right">Course Start Date</TableCell>
           
          </TableRow>
        </TableHead>
        <TableBody>
         
            <TableRow
             
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row"> {result.cName}</TableCell>
              <TableCell align="right">{result.cDesc}</TableCell>
              <TableCell align="right">{result.cDuration}</TableCell>
              <TableCell align="right">{result.cStartdate}</TableCell>



              
            </TableRow>

{/* {result.map((value,index) => (
            
            <TableRow
              key={index}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {result.cName}
              </TableCell>
              <TableCell align="right">{result.cDesc}</TableCell>
              <TableCell align="right">{result.cDuration}</TableCell>
              <TableCell align="right">{result.cStartdate}</TableCell>
               </TableRow>
            )
)
          }   
           */}
        </TableBody>
      </Table>

    </div>
  )
}

export default CourseAdd
