import { Button, Table, TableBody, TableCell, TableHead, TableRow, TextField, Typography } from '@mui/material'
import axios from 'axios'
import React, { useState } from 'react'
import useForm from './useForm'

var CourseSearch = () => {
    var[course,setCourse]=useForm({"cName":""})
    var[result,setResult]=useState([])

    const searchCourse= async(e)=>{
      alert("clicked")

       try{

        await axios.post("/api/search",course)
        .then((response)=>{
            console.log(response.data);
            setResult(result=response.data)

        })
       }
        catch(error)
        {
          console.log(error);
                }
    }
  return (
    <div>
      <Typography variant='h3'>Course Search</Typography>
      <br></br>
      <TextField value={course.cName} name='cName' onChange={setCourse} variant='outlined' label='Course Name' fullWidth margin='normal'></TextField>
      <Button onClick={searchCourse} variant='contained'color='primary' fullWidth>Search</Button>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Course Name</TableCell>
            <TableCell align="right">Course Description</TableCell>
            <TableCell align="right">Course Duration</TableCell>
            <TableCell align="right">Course Start Date</TableCell>
            <TableCell align="right">Delete Course</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {
        result.map((value,index) => {
          return<TableRow key={index}>
              <TableCell> {value.cName}</TableCell>
              <TableCell>{value.cDesc}</TableCell>
              <TableCell>{value.cDuration}</TableCell>
              <TableCell>{value.cStartdate}</TableCell>
             
            </TableRow>
})}
        </TableBody>
      </Table>
   
   
    </div>
  )
}

export default CourseSearch
